<template>
    <header class="banner">
        <h1 class="text">
            ストラスブールの雪
        </h1>
        <div class="sub">
            <a target="blank" href="https://weibo.com/u/5438808104/">微博</a>
            <a target="blank" href="https://github.com/kazusa0106">git</a>
        </div>
        <canvas id="myCanvas" class="animation">

        </canvas>
        <div class="bkg" :style="style">

        </div>
    </header>
</template>

<script>
import bkg from "@/assets/img/banner.jpg";
import initAnimation from "./banenrAnimation.js";

window.requestAnimFrame =
  window.requestAnimationFrame ||
  window.webkitRequestAnimationFrame ||
  window.mozRequestAnimationFrame ||
  function(callback) {
    window.setTimeout(callback, 1000 / 60);
  };

export default {
  data() {
    return {};
  },
  mounted() {
    initAnimation();
  },
  computed: {
    style() {
      return {
        backgroundImage: `url(${bkg})`
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.banner {
  background-color: white;
  height: 200px;
  width: 100%;
  .text {
    position: absolute;
    color: white;
    margin-left: 200px;
    z-index: 999;
  }
  .sub {
    position: absolute;
    font-size: 18px;
    font-weight: bold;
    color: #8eaad6;
    cursor: pointer;
    right: 200px;
    bottom: 20px;
    z-index: 999;
    display: flex;
    flex-direction: column;
  }
  .animation {
    position: absolute;
    height: 200px;
    z-index: 500;
    width: 100%;
  }
  .bkg {
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    height: 200px;
    opacity: 1;
  }
  position: relative;
}
</style>



