
import axios from 'axios';

export default {
    get(url, params, errCb) {
        return axios.get(url, {
            params: params,
            headers: { 'X-Requested-With': 'XMLHttpRequest' },
            withCredentials: true
        }
        ).catch((err) => {
            console.log(err);
            if (err.response.status === 401) {
                window.location.href = err.response.data.data;
            } else if (errCb) {
                errCb(err);
            } else {
                Promise.reject(err);
            }
        }).then(res => {
            if (res.data && res.data.status !== 0) {
                // this.$message.error(res.data.message);
                // this.loading = false;
                // return Promise.reject();  
                if (res.data.status === -1) {
                    return Promise.resolve(res);
                }
            }
            return res.data;
        });
    },
    post(url, params, errCb) {
        return axios.post(url, params, {
            headers: { 'X-Requested-With': 'XMLHttpRequest' },
            withCredentials: true
        }).catch((err) => {
            if (err.response.status === 401) {
                window.location.href = err.response.data.data;
            } else if (errCb) {
                errCb(err);
            } else {
                Promise.reject(err);
            }
        }).then(res => {
            if (res.data && res.data.status !== 0) {
                this.$message.error(res.data.message);
                this.loading = false;
                return Promise.reject();
            }
            return res.data;
        });
    },
    put(url, params, errCb) {
        return axios.put(url, params, {
            headers: { 'X-Requested-With': 'XMLHttpRequest' },
            withCredentials: true
        }).catch((err) => {
            if (err.response.status === 401) {
                window.location.href = err.response.data.data;
            } else if (errCb) {
                errCb(err);
            } else {
                Promise.reject(err);
            }
        }).then(res => {
            if (res.data && res.data.status !== 0) {
                this.$message.error(res.data.message);
                this.loading = false;
                return Promise.reject();
            }
            return res.data;
        });
    },
    delete(url, errCb) {
        return axios.delete(url, {
            headers: { 'X-Requested-With': 'XMLHttpRequest' },
            withCredentials: true
        }).catch((err) => {
            if (err.response.status === 401) {
                window.location.href = err.response.data.data;
            } else if (errCb) {
                errCb(err);
            } else {
                Promise.reject(err);
            }
        }).then(res => {
            if (res.data && res.data.status !== 0) {
                this.$message.error(res.data.message);
                this.loading = false;
                return Promise.reject();
            }
            return res.data;
        });
    }
};


